import React, { useCallback, useEffect, useRef, useState } from 'react'
import { AspectRatio } from '@chakra-ui/react';
import { interval } from 'd3-timer'

import Box from '../../components/Box';
import Link from '../../components/Link';
import Text from '../../components/Text';
import Flex from '../../components/Flex';
import Image from '../../components/Image';

import Face from '../Face'

// const colorsCfg = {
//   A: 'green',
//   B: 'orange',
//   C: 'pink',
// }
const rate = 0.2
const PerTrash = ({ data }) => {
  const [showFace, setShowFace] = useState(() => Math.random() < rate)
  const timer = useRef()
  const tick = useCallback(() => {
    setShowFace(Math.random() < rate)
  }, [])
  useEffect(() => {
    timer.current = interval(tick, 5000)
    return () => {
      if (timer.current) {
        timer.current.stop()
      }
    }
  }, [])
  const scale = (data.transform.homeScale || 100) / 100
  return (
    <AspectRatio ratio={1}>
      <Box p="0.25em">
        <Link to={`/trash/${data.id}`} height="100%" width="100%">
          <Box
            width="100%"
            bg="white"
            // bg={`colors.${colorsCfg[data.recycleValue]}`}
            height="100%"
            textAlign="center"
            flexDirection="column"
            transition="all 0.25s"
            _hover={{
              boxShadow: '4px 4px 0px rgba(0,0,0,0.2)',
              transform: 'translate(-4px, -4px)',
            }}
            border="1px solid black"
            rounded="1em"
            position="relative"
            overflow="hidden"
          >
            <Box.Absolute
              left="50%"
              bottom="0"
              width="100%"
              transform={`scale(${scale}) translate(${['homeX', 'homeY'].map((k, i) => `-${((i ? 0 : 50) - (data.transform[k] || 0)) / scale}%`).join()})`}
            >
              <Image src={data.img} />
              {showFace && (
                <Face id={data.transform.faceNo} transform={data.transform.face} />
              )}
            </Box.Absolute>
            <Box.Absolute width="100%" left="50%" top="0.75em" transform="translateX(-50%)">
              <Text
                // color="white"
                fontWeight="700"
                letterSpacing="0.125em"
              >{data.name}</Text>
            </Box.Absolute>
          </Box>
        </Link>
      </Box>
    </AspectRatio>
  )
}

export default PerTrash
